import React, {useReducer, useCallback, useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import {TitleAndCategoryForm, DescriptionForm, Review} from "../../components/CreatePetitionForms";
import {useDispatch, useSelector} from "react-redux";
import {createPetition} from "../../redux/actions/petitions";
import ProgressComponent from "../../components/ProgressComponent";
import {CategoryTitles} from "../../dummy-data/petitions";
import {useHistory} from 'react-router-dom';

const FORM_INPUT_UPDATE = 'FORM_INPUT_UPDATE';

const formStateReducer = (state, action) => {
  if (action.type === FORM_INPUT_UPDATE) {
    const updatedValues = {
      ...state.inputValues,
      [action.inputId]: action.value,
    };
    const updatedValidities = {
      ...state.inputValidities,
      [action.inputId]: action.isValid,
    };
    let updatedFormIsValid = true;
    for (const key in updatedValidities) {
      updatedFormIsValid = updatedFormIsValid && updatedValidities[key];
    }
    return {
      ...state,
      inputValues: updatedValues,
      inputValidities: updatedValidities,
      formIsValid: updatedFormIsValid,
    };
  }
  return state;
};

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  layout: {
    width: 'auto',
    marginLeft: theme.spacing(2),
    marginRight: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(2) * 2)]: {
      width: 600,
      marginLeft: 'auto',
      marginRight: 'auto',
    },
  },
  paper: {
    width: '60%',
    minWidth: '320px',
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
    padding: theme.spacing(2),
    [theme.breakpoints.up(600 + theme.spacing(3) * 2)]: {
      marginTop: theme.spacing(6),
      marginBottom: theme.spacing(6),
      padding: theme.spacing(3),
    },
  },
  stepper: {
    padding: theme.spacing(3, 0, 5),
  },
  buttons: {
    display: 'flex',
    justifyContent: 'flex-end',
  },
  button: {
    marginTop: theme.spacing(3),
    marginLeft: theme.spacing(1),
  },
}));

const steps = ['–í–∏–±—ñ—Ä –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó', '–¢—ñ–ª–æ –ø–µ—Ç–∏—Ü—ñ—ó', '–ü–µ—Ä–µ–≤—ñ—Ä–∫–∞ –¥–∞–Ω–∏—Ö'];

const CreatePetitionPage = () => {
  const history = useHistory();
  const isLoading = useSelector(state => state.petitions.addedPetitionLoading);
  const petitionUrl = useSelector(state => state.petitions.addedPetitionUrl);
  const dispatch = useDispatch();

  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);

  const [formState, dispatchFormState] = useReducer(formStateReducer, {
    inputValues: {
      title: '',
      category: '',
      description: '',
    },
    inputValidities: {
      title: false,
      category: false,
      description: true,
    },
    formIsValid: false,
  });

  const inputChangeHandler = useCallback((inputId, inputValue, inputValidity) => {
      dispatchFormState({
        type: FORM_INPUT_UPDATE,
        inputId: inputId,
        value: inputValue,
        isValid: inputValidity,
      });
    },
    [dispatchFormState],
  );

  const getStepContent = (step) => {
    switch (step) {
      case 0:
        return <TitleAndCategoryForm onInputChange={inputChangeHandler}
                                   categoryValue={formState.inputValues.category}
                                   titleValue={formState.inputValues.title}
                                   errorText="–ë—É–¥—å –ª–∞—Å–∫–∞ –≤–≤–µ–¥—ñ—Ç—å –∑–∞–≥–æ–ª–æ–≤–æ–∫ –ø–µ—Ç–∏—Ü—ñ—ó –¥–æ–≤–∂–∏–Ω–æ—é –≤—ñ–¥ 10 –¥–æ 100 —Å–∏–º–≤–æ–ª—ñ–≤"/>;
      case 1:
        return <DescriptionForm onInputChange={inputChangeHandler}
                            descriptionValue={formState.inputValues.description}
                            errorText="–ë—É–¥—å –ª–∞—Å–∫–∞ –≤–≤–µ–¥—ñ—Ç—å –æ–ø–∏—Å –ø–µ—Ç–∏—Ü—ñ—ó –≤—ñ–¥ 50 –¥–æ 500 —Å–∏–º–≤–æ–ª—ñ–≤" />;
      case 2:
        return <Review title={formState.inputValues.title} category={CategoryTitles[formState.inputValues.category - 1].title}
                       description={formState.inputValues.description}/>;
      default:
        throw new Error('–í–∏–Ω–∏–∫–ª–∞ –ø–æ–º–∏–ª–∫–∞');
    }
  };

  const handleNext = () => {
    //send request if
    if(activeStep === 2) {
      if(!formState.formIsValid) {
      } else {
        dispatch(createPetition(formState.inputValues.title,
          formState.inputValues.category, formState.inputValues.description));
      }
    }
    if(activeStep === steps.length) {
      history.push(`/petitions/${petitionUrl}`);
    }
    setActiveStep(activeStep + 1);
  };

  const handleBack = () => {
    setActiveStep(activeStep - 1);
  };

  return (
    <Paper className={classes.paper}>
      <Typography component="h1" variant="h4" align="center">
        –°—Ç–≤–æ—Ä–µ–Ω–Ω—è –ø–µ—Ç–∏—Ü—ñ—ó
      </Typography>
      <Stepper activeStep={activeStep} className={classes.stepper}>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <React.Fragment> {/*if this is last step + 1*/}
        {activeStep === steps.length ? isLoading ? (<ProgressComponent text='–°—Ç–≤–æ—Ä—é—î–º–æ –ø–µ—Ç–∏—Ü—ñ—é, –∑–∞—á–µ–∫–∞–π—Ç–µ.'/>) : (
          <React.Fragment>
            <Typography variant="h5" gutterBottom>
              –î—è–∫—É—î–º–æ –∑–∞ –æ–ø—É–±–ª—ñ–∫—É–≤–∞–Ω–Ω—è –ø–µ—Ç–∏—Ü—ñ—ó.
            </Typography>
            <Typography variant="subtitle1">
              {`–ü–æ—Å–∏–ª–∞–Ω–Ω—è –Ω–∞ –ø–µ—Ç–∏—Ü—ñ—é: ${petitionUrl}`}
            </Typography>
          </React.Fragment>
        ) : (
          <React.Fragment>
            {/*buttons*/}
            {getStepContent(activeStep)}
            <div className={classes.buttons}>
              {activeStep != 0 && activeStep != steps.length && (
                <Button onClick={handleBack} className={classes.button}>
                  –ù–∞–∑–∞–¥
                </Button>
              )}
              <Button
                variant="contained"
                color="primary"
                onClick={handleNext}
                className={classes.button}
                disabled={activeStep === 0 ? !(formState.inputValidities.title && formState.inputValidities.category) :
                activeStep === 1 ? !formState.inputValidities.description : false}
              >
                {activeStep === steps.length - 1 ? '–û–ø—É–±–ª—ñ–∫—É–≤–∞—Ç–∏ –ø–µ—Ç–∏—Ü—ñ—é' : activeStep === steps.length ?
                  '–ù–∞ —Å—Ç–æ—Ä—ñ–Ω–∫—É –ø–µ—Ç–∏—Ü—ñ—ó' : '–î–∞–ª—ñ'}
              </Button>
            </div>
          </React.Fragment>
        )}
      </React.Fragment>
    </Paper>
  )
};

export default CreatePetitionPage;
